/* eslint-disable no-restricted-syntax */
/* eslint-disable import/prefer-default-export */
import { PATHS } from "./WordSearch";

export const strReverse = (str) => str.split("").reverse().join("");

const isPalindrome = (word) => word === word.split("").reverse().join("");

const matrixColumn = (matrix, i) => matrix.map((row) => row[i]);

const searchRows = (matrix, needle, reverse = false) => {
  let count = 0;
  for (let line of matrix) {
    line = line.join("");
    if (reverse) line = strReverse(line);
    count += (line.match(new RegExp(needle, "g")) || []).length;
  }
  return count;
};

const searchColumns = (matrix, needle, reverse = false) => {
  let count = 0;
  for (let i = 0; i < matrix[0].length; i++) {
    let line = matrixColumn(matrix, i).join("");
    if (reverse) line = strReverse(line);
    count += (line.match(new RegExp(needle, "g")) || []).length;
  }
  return count;
};

/** Return big answers, that contain other answers with count: [apple, app, ap] => [apple, app]
 * @return {{ [someWord: String]: Number}}
 */
const getNestedAnswers = (answers) => {
  const big = [];
  for (const i in answers) {
    const a = answers[i];
    for (const j in answers) {
      const b = answers[j];
      if (i === j);
      if (a.includes(b)) {
        big.push([b, a]);
      }
    }
  }

  const count = {};
  for (const word of big) {
    count[word[0]] = (count[word[0]] || 0) + 1;
  }
  // remove all small words that don't have even smaller words
  // [abc, ab, c] => c gets deleted here
  for (const w in count) {
    if (!big.find((el) => el[1] === w && el[0] !== el[1])) {
      delete count[w];
    }
  }
  return count;
};

/**
 * Check if matrix contains a repeated long word.
 *
 * A long word is a word (answer) that contains other answers
 *
 * example: answers = ['app', 'apple'] if the matrix has 'apple' twice or more it will be rejected
 *
 * the alternative is to find all positions of answers in the matrix keep track where each instance is
 * and whether it was selected before.
 *
 * basically all words can repeat, only big words can not.
 *
 * @param {number[][]} matrix
 * @param {string[]} answers
 * @param {PATHS[]} paths
 */
export const verifyMatrix = (matrix, answers, paths) => {
  const found = {};
  answers = [...new Set(answers)];
  const wordsCount = getNestedAnswers(answers);
  for (const answer of answers) {
    let count = 0;
    if (paths.includes(PATHS.horizontal)) count += searchRows(matrix, answer);
    if (paths.includes(PATHS.vertical)) count += searchColumns(matrix, answer);
    // palindromes are same when reversed messing up the count
    if (!isPalindrome(answer)) {
      if (paths.includes(PATHS.horizontalBack)) {
        count += searchRows(matrix, answer, true);
      }
      if (paths.includes(PATHS.verticalUp)) {
        count += searchColumns(matrix, answer, true);
      }
    }
    found[answer] = count;
  }

  for (const word in wordsCount) {
    if (found[word] !== wordsCount[word]) return false;
  }
  return true;
};

