import React, { useEffect, useState } from "react";
import { PATHS } from "./WordSearch";

const strReverse = (str) => str.split("").reverse().join("");

const getWordRange = (search, needle) => {
  let result;
  const indexes = [];
  const regex = new RegExp(needle, "g");
  while ((result = regex.exec(search))) {
    indexes.push(result.index);
  }
  return indexes.map((i) => [i, i + needle.length - 1]);
};

const inRange = (i, range) => i >= range[0] && i <= range[1];

const WordGrid = (props) => {
  const {
    markedBackgroundColor,
    selectedBackgroundColor,
    hoveredBackgroundColor,
    backgroundColor,
    markedFontColor,
    selectedFontColor,
    hoveredFontColor,
    fontColor
  } = props.design;

  const {
    answerWords,
    matrix,
    isSelecting,
    setIsSelecting,
    availablePaths,
    selectedLetters,
    setSelectedLetters,
    markedLetters,
    setMarkedLetters,
    gameCompleted,
    setSelectedWord
  } = props.options;

  const [data, setData] = useState([]);
  const [path, setPath] = useState();
  const [hover, setHover] = useState();
  const [selectedBeforeWords, setSelectedBeforeWords] = useState([]);
  const longTouchTimeout = useState(undefined);

  const unique = (arr, keyProps) => {
    const kvArray = arr.map((entry) => {
      const key = keyProps.map((k) => entry[k]).join("|");
      return [key, entry];
    });
    const map = new Map(kvArray);
    return Array.from(map.values());
  };

  const markLetters = (param) => {
    setMarkedLetters(unique([...markedLetters, ...param], ["row", "column"]));
  };

  function checkLongerAnswerLine(
    answer,
    selectedWord,
    line,
    reverse,
    selectionDetail,
    isRow
  ) {
    line = line.join("");
    if (!answer.includes(selectedWord)) return false;
    const detailPropName = isRow ? "column" : "row";

    if (reverse) line = strReverse(line);

    const foundRanges = getWordRange(line, answer);
    const matchedAnyRange = foundRanges.some((range) => {
      const selectionStart = selectionDetail[0][detailPropName];
      const selectionEnd =
        selectionDetail[selectionDetail.length - 1][detailPropName];
      if (inRange(selectionStart, range) || inRange(selectionEnd, range)) {
        return true;
      }
      return false;
    });
    if (matchedAnyRange) return true;
  }

  const getLongerAnswers = (selectedWord, param, paths, selectionDetail) => {
    const matrixColumn = (matrix, i) => matrix.map((row) => row[i]);
    const isColumn = param[0].column === param[1].column;
    const isRow = !isColumn;
    const row = matrix[param[0].row];
    const column = matrixColumn(matrix, param[0].column);

    // find any answer that includes selected answer, in a later step
    // this is filtered to exlude words that are not already selected.
    const longerAnswers = answerWords.filter((word) => {
      // args to be passed except line, inverse
      const getArgs = (line, inverse) => [
        word,
        selectedWord,
        line,
        inverse,
        selectionDetail,
        isRow
      ];
      if (word === selectedWord) return false;
      const line = isRow ? row : column;
      if (paths.includes(PATHS.horizontal)) {
        if (checkLongerAnswerLine(...getArgs(line, false))) return true;
      }
      if (paths.includes(PATHS.horizontalBack)) {
        if (checkLongerAnswerLine(...getArgs(line, true))) return true;
      }
      return false;
    });
    return longerAnswers || [];
  };

  const addToSelectedBefore = (word) => {
    setSelectedBeforeWords([...selectedBeforeWords, word]);
  };

  const isSelectedBefore = (word) => {
    const answerNotSelected = answerWords.filter(
      (word) => !selectedBeforeWords.includes(word)
    );
    return answerNotSelected.includes(word);
  };

  const isAnswer = (param) => {
    const selectedWord = param.map((x) => x.letter).join("");
    let found = false;
    for (let i = 0; i < answerWords.length; i++) {
      const element = answerWords[i];
      if (selectedWord === element) {
        const longerWords = getLongerAnswers(
          selectedWord,
          param,
          availablePaths,
          param
        );
        if (
          !longerWords.length ||
          longerWords.some((long) => !isSelectedBefore(long))
        ) {
          found = true;
          markLetters(param);
          addToSelectedBefore(selectedWord);
          setSelectedWord(selectedWord);
          break;
        }
      }
    }
    return found;
  };

  const removeLetterFromList = (letter) => {
    const tmp = selectedLetters.filter(
      (element) =>
        letter.row !== element.row || letter.column !== element.column
    );
    setSelectedLetters(tmp);
  };

  const isBeforeSelect = (letter, before) => {
    let result = false;
    if (
      (letter.column + 1 === before.column && letter.row === before.row) ||
      (letter.column - 1 === before.column && letter.row === before.row) ||
      (letter.row + 1 === before.row && letter.column === before.column) ||
      (letter.row - 1 === before.row && letter.column === before.column)
    ) {
      result = true;
    }

    return result;
  };

  const isAvailablePath = (searched) => {
    let found = false;

    if (availablePaths.length > 0) {
      for (let i = 0; i < availablePaths.length; i += 1) {
        const element = availablePaths[i];
        if (searched === element) {
          found = true;
          break;
        }
      }
    }

    return found;
  };

  const chosePath = (item) => {
    let result = "horizontal";
    const lastLetter = selectedLetters.slice(-2)[0];
    const letter = item !== undefined ? item : selectedLetters.slice(-1)[0];
    if (
      lastLetter.row === letter.row &&
      lastLetter.column - 1 === letter.column
    ) {
      result = "horizontalBack";
    } else if (
      lastLetter.row === letter.row &&
      lastLetter.column + 1 === letter.column
    ) {
      result = "horizontal";
    } else if (
      lastLetter.column === letter.column &&
      lastLetter.row + 1 === letter.row
    ) {
      result = "vertical";
    } else if (
      lastLetter.column === letter.column &&
      lastLetter.row - 1 === letter.row
    ) {
      result = "verticalUp";
    }

    return result;
  };

  const isConnected = (letter, before) => {
    let result = false;
    if (selectedLetters.length < 1) {
      result = true;
    } else if (selectedLetters.length === 1 && isBeforeSelect(letter, before)) {
      setPath(chosePath(letter));
      result = true;
    } else if (
      path === "horizontalBack" &&
      isAvailablePath(path) &&
      before.row === letter.row &&
      (before.column - 1 === letter.column || before.column === letter.column)
    ) {
      result = true;
    } else if (
      path === "horizontal" &&
      isAvailablePath(path) &&
      before.row === letter.row &&
      (before.column + 1 === letter.column || before.column === letter.column)
    ) {
      result = true;
    } else if (
      path === "vertical" &&
      isAvailablePath(path) &&
      before.column === letter.column &&
      (before.row + 1 === letter.row || before.row === letter.row)
    ) {
      result = true;
    } else if (
      path === "verticalUp" &&
      isAvailablePath(path) &&
      before.column === letter.column &&
      (before.row - 1 === letter.row || before.row === letter.row)
    ) {
      result = true;
    } else {
      result = false;
      setSelectedLetters([]);
    }
    return result;
  };

  const addFirstLetter = (letter) => {
    setSelectedLetters([letter]);
  };

  const isSelected = (searched) => {
    let found = false;

    if (selectedLetters.length > 0) {
      for (let i = 0; i < selectedLetters.length; i++) {
        const element = selectedLetters[i];
        if (
          searched.row === element.row &&
          searched.column === element.column
        ) {
          found = true;
          break;
        }
      }
    }

    return found;
  };

  const addLetterToSelectedWords = (letter) => {
    if (isSelecting) {
      const result = isSelected(letter);
      const before = selectedLetters.slice(-1)[0];
      if (result === false && isConnected(letter, before)) {
        setSelectedLetters([...selectedLetters, letter]);
      } else {
        const before = selectedLetters.slice(-1)[0];
        if (isBeforeSelect(letter, before)) {
          removeLetterFromList(before);
        }
      }
    }
  };

  const isMarked = (searched) => {
    let found = false;

    if (markedLetters.length > 0) {
      for (let i = 0; i < markedLetters.length; i++) {
        const element = markedLetters[i];
        if (
          searched.row === element.row &&
          searched.column === element.column
        ) {
          found = true;
          break;
        }
      }
    }
    return found;
  };

  const handleMouseEnter = (j) => {
    addLetterToSelectedWords(j);
    setHover(j);
  };

  const handleMouseDown = (j) => {
    addFirstLetter(j);
    setIsSelecting(true);
  };

  const handleSelection = () => {
    setIsSelecting(false, isAnswer(selectedLetters));
  };

  const handleTouchMove = (e) => {
    const x = e.targetTouches[0].clientX;
    const y = e.targetTouches[0].clientY;
    const el = document.elementFromPoint(x, y);
    if (!el || !el.dataset) return;
    const jstr = el.dataset.j;
    if (!jstr) return;
    const j = JSON.parse(jstr);
    handleMouseEnter(j);
  };
  const handleTouchStart = (j) => {
    longTouchTimeout.current = setTimeout(() => {
      document.body.style.overflow = "hidden";
    }, 200);
    handleMouseDown(j);
  };
  const handleTouchEnd = () => {
    handleSelection();
    setTimeout(setHover());
    setTimeout(() => {
      clearTimeout(longTouchTimeout.current);
      document.body.style.overflow = "auto";
      longTouchTimeout.current = undefined;
    }, 10);
  };

  useEffect(() => {
    if (!isSelecting) {
      setPath();
      setSelectedLetters([]);
    }
  }, [isSelecting]);

  useEffect(() => {
    const tmp = matrix.map((row, i) =>
      row.map((column, j) => ({
        letter: column,
        row: i,
        column: j
      }))
    );
    setData(tmp);
  }, []);

  return (
    <table
      onMouseLeave={handleSelection}
      onTouchMove={handleTouchMove}
      onTouchEnd={handleTouchEnd}
    >
      <tbody>
        {data.map((i, row) => (
          <tr key={row}>
            {i.map((j, column) => (
              <td
                key={column}
                onMouseLeave={() => setHover()}
                onMouseEnter={() => handleMouseEnter(j)}
                onMouseDown={
                  gameCompleted || isMarked(j) ? null : () => handleMouseDown(j)
                }
                onMouseUp={() => handleSelection()}
                onTouchStart={
                  gameCompleted || isMarked(j)
                    ? null
                    : () => handleTouchStart(j)
                }
                data-j={JSON.stringify(j)}
                className="letter-wrapper"
                style={{
                  backgroundColor:
                    isMarked(j) === true
                      ? markedBackgroundColor
                      : isSelected(j) === true
                      ? selectedBackgroundColor
                      : j === hover
                      ? hoveredBackgroundColor
                      : backgroundColor
                }}
              >
                <h3
                  style={{
                    padding: 10,
                    fontFamily: "monospace",
                    fontSize: 20,
                    color:
                      isMarked(j) === true
                        ? markedFontColor
                        : isSelected(j) === true
                        ? selectedFontColor
                        : j === hover
                        ? hoveredFontColor
                        : fontColor
                  }}
                >
                  {j.letter}
                </h3>
              </td>
            ))}
          </tr>
        ))}
      </tbody>
    </table>
  );
};

export default WordGrid;
