import './App.css';
import WordSearch from './WordSearch';
import {db} from "./db";
import { useLiveQuery } from "dexie-react-hooks";

const WORD_AUDIO = [
  {
    word: "success",
    sound:
      "https://cors-anywhere.herokuapp.com/https://ssl.gstatic.com/dictionary/static/sounds/oxford/success--_us_1.mp3"
  },
  {
    word: "example",
    sound:
      "https://cors-anywhere.herokuapp.com/https://ssl.gstatic.com/dictionary/static/sounds/oxford/example--_us_1.mp3"
  },
  {
    word: "word",
    sound:
      "https://cors-anywhere.herokuapp.com/https://ssl.gstatic.com/dictionary/static/sounds/oxford/word--_us_1.mp3"
  },
  {
    word: "sound",
    sound:
      "https://cors-anywhere.herokuapp.com/https://ssl.gstatic.com/dictionary/static/sounds/oxford/sound--_us_1.mp3"
  }
];

function App() {
  const wordSounds = useLiveQuery(() => db.words.toArray());
  const getWords = () => {
    console.log(wordSounds);
  };
  const fetchAudio = () => {
    Promise.all(
      WORD_AUDIO.map((item) =>
        fetch(item.sound)
          .then((resp) => resp.blob())
          .then((blob) => {
            db.words.add({
              word: item.word,
              sound: blob
            });
          })
      )
    );
  };
  return (
    <div className="App">
      <button onClick={() => fetchAudio()}>fetch word audio</button>
      <button onClick={() => getWords()}>check db</button>
      <WordSearch />
    </div>
  );
}

export default App;
