import React, { useState, useEffect, useRef, useCallback } from "react";
import { useLiveQuery } from "dexie-react-hooks";
import Sound from "react-sound";
import WordGenerator from "./WordGenerator";
import WordGrid from "./WordGrid";
import { verifyMatrix } from "./verifyGrid";
import { db } from "./db";
import "./styles.scss";

const WORDS = ["word", "example", "sound"];

export const PATHS = {
  horizontal: "horizontal",
  horizontalBack: "horizontalBack",
  vertical: "vertical",
  verticalUp: "verticalUp"
};

const matchWordByIndex = (word, wordSounds) => {
  const regex = new RegExp(`\\b${word}\\b`);
  if (wordSounds !== undefined) {
    const wordReg = wordSounds.map((item) => item.word.match(regex));
    const findWord = wordReg.findIndex((el) => el !== null);
    return findWord;
  }
};

const WordSearch = () => {
  const availablePaths = [
    PATHS.horizontal,
    PATHS.horizontalBack,
    PATHS.vertical,
    PATHS.verticalUp
  ];

  const isTouchDevice = () =>
    "ontouchstart" in window ||
    navigator.maxTouchPoints > 0 ||
    navigator.msMaxTouchPoints > 0;
  const isTouch = isTouchDevice();

  const wordSounds = useLiveQuery(() => db.words.toArray());

  const [isSelecting, setIsSelecting] = useState(false);
  const [selectedLetters, setSelectedLetters] = useState([]);
  const [markedLetters, setMarkedLetters] = useState([]);
  const [gameCompleted, setGameCompleted] = useState(false);
  const [results, setResults] = useState([]);
  const [selectedWord, setSelectedWord] = useState(false);

  const [wordSound, setWordSound] = useState("");
  const [wordStatus, setWordStatus] = useState(Sound.status.STOPPED);
  const [successSound, setSuccessSound] = useState("");
  const [successStatus, setSuccessStatus] = useState(Sound.status.STOPPED);

  const wordPuzzleMatrix = () =>
    WordGenerator.wordfind.newPuzzle(WORDS, {
      orientations: availablePaths,
      width: 12,
      height: 12,
      preferOverlap: false
    });

  // try to make valid matrix, max 10 retries
  // if all retries fail, the user will have to fill the big words first, then small words
  // check verifyMatrix.js for more info
  const tryMatrix = () => {
    let matrix;
    for (let i = 0; i < 10; i++) {
      matrix = wordPuzzleMatrix();
      if (verifyMatrix(matrix, WORDS, availablePaths)) {
        return matrix;
      }
    }
    console.log(
      "unable to make clear matrix, for answers contained within answers, the user might have to select the longer one first."
    );
    return matrix;
  };

  const matrixRef = useRef(tryMatrix());
  // matrixRef.current = ["word".split("")];

  const playWord = (word) => {
    setSuccessStatus(Sound.status.STOPPED);
    const findWord = matchWordByIndex(word, wordSounds);
    const fileBlob = URL.createObjectURL(wordSounds[findWord].sound);
    setWordSound(fileBlob);
    setWordStatus(Sound.status.PLAYING);
  };

  const playTouch = (word) => {
    const findWord = matchWordByIndex(word, wordSounds);
    const fileBlob = URL.createObjectURL(wordSounds[findWord].sound);
    return new Audio(fileBlob).play();
  };

  const handleLastResult = () => {
    if (gameCompleted) {
      setWordStatus(Sound.status.STOPPED);
      const findWord = matchWordByIndex('success', wordSounds);
      const fileBlob = URL.createObjectURL(wordSounds[findWord].sound)
      setSuccessSound(fileBlob);
      setSuccessStatus(Sound.status.PLAYING);
    }
  };
  const setSelectedWordRoot = (selectedWord) => {
    const isWordInResults = results.includes(selectedWord);
    console.log(isWordInResults, selectedWord)
    if (!isWordInResults && selectedWord) {
      setResults((current) => [...current, selectedWord]);
      playTouch(selectedWord)
    }
    setSelectedWord(selectedWord);
  }
  
  useEffect(() => {
    if (isSelecting) {
      setSuccessSound(Sound.status.STOPPED);
      setWordStatus(Sound.status.STOPPED);
    }
  }, [isSelecting, selectedWord, isTouch]);

  useEffect(() => {
    console.log(results, WORDS.length, 'lengthy things')
    if (results.length === WORDS.length) {
        setGameCompleted(true);
    }
}, [results]);

  return (
    <div>
      <WordGrid
        design={{
          markedBackgroundColor: "#F6E3C5",
          selectedBackgroundColor: "#A0D995",
          backgroundColor: "#6CC4A1",
          markedFontColor: "#4CACBC",
          selectedFontColor: "#4CACBC",
          hoveredFontColor: "#6CC4A1",
          fontColor: "#fff"
        }}
        options={{
          answerWords: WORDS,
          matrix: matrixRef.current,
          isSelecting,
          selectedLetters,
          setSelectedLetters,
          markedLetters,
          setMarkedLetters,
          setIsSelecting,
          setSelectedWord: setSelectedWordRoot,
          availablePaths,
          gameCompleted
        }}
      />
      <>
        <Sound
          url={wordSound}
          playStatus={wordStatus}
          onFinishedPlaying={() => handleLastResult()}
        />
        <Sound
          url={successSound}
          playStatus={successStatus}
        />
      </>
    </div>
  );
};

export default WordSearch;
